import React from 'react';
import { useHistory, useParams } from 'react-router';
import styled from 'styled-components';
import './Detail.css';

let Box = styled.div`
  padding : 20px;
  background: gray;
`;
let Title = styled.h4`
  font-size : 25px;
  color: ${props => props.color};
`;

function Detail(props) {

    let { id } = useParams();
    let find = props.shoes.find(function(상품){
      return 상품.id == id
    });
  
    let history= useHistory();

    return (
        <div className="container">
      <Box>
        <Title className='red'>HI Its Test</Title>
      </Box>
            <div className="row">
                <div className="col-md-6">
                    <img
                        src={"https://codingapple1.github.io/shop/shoes"+(find.id+1)+".jpg"}
                        width="100%"
                    />
                </div>
                <div className="col-md-6 mt-4">
                    <h4 className="pt-5">{find.title}</h4>
                    <p>{find.content}</p>
                    <p>{find.price}</p>
                    <Info stock = {props.stock}></Info>
                    <button className="btn btn-danger" onClick={()=>{
                      props.setStock([9,10,12]);
                    }}>주문하기</button>
                    <button className="btn btn-danger" onClick={()=>{
                        history.goBack();
                    }}>뒤로가기</button>
                </div>
            </div>
        </div>
    );
}

function Info(props){
  console.log(props);
  return(
    <p>stock : {props.stock[0]}</p>
  )
}
export default Detail;